.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #1a1a2e;
  padding: 1rem 2rem;
  flex-wrap: nowrap; 
  overflow-x: auto;   
}

.logo-image {
  height: clamp(30px, 5vw, 50px);
  width: auto;
}

.logo-link {
  display: flex;
  align-items: center;
  text-decoration: none;
}

.nav-links {
  list-style: none;
  display: flex;
  gap: clamp(1rem, 2vw, 2rem);
  margin-left: auto;
  align-items: center;
  flex-wrap: nowrap;    
  overflow: hidden;    
}

.nav-links li a {
  font-size: clamp(0.9rem, 2vw, 1.5rem);
  font-weight: 600;
  color: #5bc0ff;
  transition: color 0.3s ease;
  text-decoration: none;
  white-space: nowrap;   
}

.nav-links li a:hover {
  color: var(--color-sky-blue);
}

@media (max-width: 768px) {
  .navbar {
    flex-direction: column;
    align-items: flex-start;
    padding: 1rem;
    overflow: visible;
  }

  .nav-links {
    flex-direction: column;
    width: 100%;
    gap: 1rem;
    margin: 1rem 0 0;
    flex-wrap: wrap;
  }

  .nav-links li a {
    font-size: 1.2rem;
  }
}
